const consecutiveSpace = / {2,}/;

/**
 * 
 * @param {string} classString 
 * @returns {string} Clean class string
 */
export function stripClassWhitespace(classString) {
	return classString.replace(consecutiveSpace, ' ').trimRight();
}

/**
 * Filters out falsy classes.
 * @param {...(string | false | null)} args The classes to be filtered
 * @return {string} The classes without the falsy values
 */
export function classes(...args) {
	return args.filter(cls => !!cls).join(' ');
}

/**
 * 
 * @param { Object } target 
 * @param { Map } map 
 * @returns { Object }
 */
export function deepClone(target, map = new Map()) {
	// 
	if (typeof target === 'object' && target !== null) {
		let cache = map.get(target) //  
		if (cache) return cache
		let isArray = Array.isArray(target) // 
		const result = isArray ? [] : {}
		map.set(target, result) // 
		if (isArray) {
			// 
			target.forEach((item, index) => {
				result[index] = deepClone(item, map)
			})
		} else {
			//  
			Object.keys(target).forEach(key => {
				result[key] = deepClone(target[key], map)
			})
		}
		return result
	} else {
		return target
	}
}

export function objectAssign(target) {
	for (let i = 1, j = arguments.length; i < j; i++) {
		let source = arguments[i] || {};
		for (let prop in source) {
			if (source.hasOwnProperty(prop)) {
				let value = source[prop];
				if (value !== undefined) {
					target[prop] = value;
				}
			}
		}
	}

	return target;
}

export function getPropByPath(obj, path, strict = undefined) {
	let tempObj = obj;
	path = path.replace(/\[(\w+)\]/g, '.$1');
	path = path.replace(/^\./, '');

	let keyArr = path.split('.');
	let i = 0;
	for (let len = keyArr.length; i < len - 1; ++i) {
		if (!tempObj && !strict) break;
		let key = keyArr[i];
		if (key in tempObj) {
			tempObj = tempObj[key];
		} else {
			if (strict) {
				throw new Error('please transfer a valid prop path to form item!');
			}
			break;
		}
	}
	return {
		o: tempObj,
		k: keyArr[i],
		v: tempObj ? tempObj[keyArr[i]] : null
	};
}
/**
 * 
 * @param { String } str
 * @param { Number } size
 * @returns {String}
 */
export function truncate(str, size) {
	return str.slice(0, size) + '...'
}

/**
 * 
 * @param { Array } arr
 * @returns {Array}
 */
export function flatten(arr) {
	let result = [...arr]
	while (result.some(item => Array.isArray(item))) {
		result = [].concat(...result)
	}
	return result
}

/**
 * 
 * @param { Array } arr 
 * @param { Number } size 
 * @returns {Array}
 */
export function chunk(arr, size = 1) {
	let result = []
	let tmp = []

	arr.forEach(item => {
		if (tmp.length === 0) {
			result.push(tmp)
		}

		tmp.push(item)

		if (tmp.length === size) {
			tmp = []
		}
	})
	return result
}

/**
 * 
 * @param { Array } arr1
 * @param { Array } arr2
 * @returns { Array }
 */
export function difference(arr1, arr2 = []) {
	if (arr1.length === 0) return []
	if (arr2.length === 0) return arr1.slice()
	return arr1.filter(item => !arr2.includes(item))
}

/**
 * 
 * @param objs
 * @returns {{}}
 */
export function mergeObject(...objs) {
	const result = {}

	objs.forEach(obj => {
		Object.keys(obj).forEach(key => {
			if (Object.prototype.hasOwnProperty.call(result, key)) {
				result[key] = [].concat(result[key], obj[key])
			} else {
				result[key] = obj[key]
			}
		})
	})
	return result
}

/**
 * 
 * 1
 * 2
 * 3  list
 * @param list 
 * @param current 
 * @param count 
 * @return { Array }
 */
export const filterMidArray = (list, current, count) => {
	let result = [] // 
	const preResult = [] // 
	const total = list.length // 
	current = Number(current)
	const mid = Math.floor(count/2) // 
	for (let i = current-mid;i < current-mid + count; i++) {
		preResult.push(i)
	}
	//  
	result = preResult.filter(el => list.includes(el))
	let len = result.length
	const isInHead = preResult[preResult.length-1] > total // 
	// 
	while (len < count) {

		if (isInHead) {
			const num = result[0] - (count - len)
			result.unshift(num)
			len++
		} else {
			len++
			result.push(len)
		}
	}
	return result
}
